<template>
  <div>
    <h2 class="title is-2">Your Address</h2>
    <form v-on:submit.prevent="updateUserData">
      <div v-if="!$store.state.user.logged || $store.state.user.logged!==true">
        <div class="columns">
          <div class="column is-12">
            Already registered? <router-link v-bind:to="{name:'login'}" tag="a" class="" active-class="is-active">Log in</router-link>
          </div>
        </div>
        <div class="columns is-variable is-2 is-centered">
          <div class="column is-6">
            <div class="field">
              <label class="label">Email</label>
              <div class="control">
                <input class="input" type="text" v-model="newUserEmail" placeholder="Email">
              </div>
            </div>
          </div>
          <div class="column is-6">
            <div class="field">
              <label class="label">Email Confirm</label>
              <div class="control">
                <input class="input" type="text" placeholder="Email Confirm">
              </div>
            </div>
          </div>
        </div>
        <div class="columns is-variable is-2 is-centered">
          <div class="column is-6">
            <div class="field">
              <label class="label">Password</label>
              <div class="control">
                <input class="input" type="password" v-model="newUserPassword" placeholder="Password">
              </div>
            </div>
          </div>
          <div class="column is-6">
            <div class="field">
              <label class="label">Password Confirm</label>
              <div class="control">
                <input class="input" type="password" placeholder="Password Confirm">
              </div>
            </div>
          </div>
        </div>
        <div class="columns">
          <div class="column is-12">
            <hr>
          </div>
        </div>
      </div>
      <div v-else>
        Logged in as {{ user.username ? user.username : '' }}
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-6">
          <div class="field">
            <label class="label">First Name</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.nameFirst" placeholder="First Name">
            </div>
          </div>
        </div>
        <div class="column is-6">
          <div class="field">
            <label class="label">Last Name</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.nameLast" placeholder="Last Name">
            </div>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-12">
          <div class="field">
            <label class="label">Street</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.street" placeholder="Street">
            </div>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-12">
          <div class="field">
            <label class="label">Street 2</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.street2" placeholder="Street 2">
            </div>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-4">
          <div class="field">
            <label class="label">ZIP</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.zip" placeholder="ZIP">
            </div>
          </div>
        </div>
        <div class="column is-8">
          <div class="field">
            <label class="label">City</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.city" placeholder="City">
            </div>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2">
        <div class="column is-6">
          <div class="field">
            <label class="label">State</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.state" placeholder="State">
            </div>
          </div>
        </div>
        <div class="column is-6">
          <div class="field">
            <label class="label">Country</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.country" placeholder="Country">
            </div>
          </div>
        </div>
      </div>

      <div class="columns is-variable is-2 is-centered">
        <div class="column is-6">
          <div class="field">
            <label class="label">Phone</label>
            <div class="control">
              <input class="input" type="text" v-model="user.addressInvoice.phone" placeholder="Phone">
            </div>
          </div>
        </div>
      </div>

      <button class="button is-success" type="submit">
        Set delivery and payment
      </button>
    </form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: "OrderUser",
  data () {
    return {
      newUserEmail: '',
      newUserPassword: ''
    }
  },
  computed: {
    ...mapGetters({
      user: 'getUserProfile'
    })
  },
  methods: {
    updateUserData: function() {
      console.log( 'this.user', this.user );
      // console.log( this.$parent.orderParams );
      if (this.user) {
        if ( this.newUserEmail && this.newUserEmail!='' ) {
          this.user.addressInvoice.email = this.newUserEmail;
          this.$store.state.user.email = this.newUserEmail;
          this.$store.state.password = this.newUserPassword;
          this.$parent.orderParams.order.user.email = this.newUserEmail;
        }
        this.$parent.orderParams.order.addresses = {
          invoiceAddress: this.user.addressInvoice
        };
      }
      this.$parent.updateOrder();
      // update user name if different from the one stored
      console.log('this.user.addressInvoice', this.user.addressInvoice);
      if ( this.user && this.user!=null && this.user.addressInvoice && this.user.addressInvoice.nameFirst && this.user.addressInvoice.nameLast 
        && this.$store.state.user && this.$store.state.user.profile && this.$store.state.user.profile.addressInvoice.nameFirst 
        && this.$store.state.user.profile.addressInvoice.nameLast ) {
          if ( this.$store.state.user.profile.addressInvoice.nameFirst != this.user.addressInvoice.nameFirst ) {
            this.$store.state.user.profile.addressInvoice.nameFirst = this.user.addressInvoice.nameFirst;
          }
          if ( this.$store.state.user.profile.addressInvoice.nameLast != this.user.addressInvoice.nameLast ) {
            this.$store.state.user.profile.addressInvoice.nameLast = this.user.addressInvoice.nameLast;
          }
      }
      let userAddressUpdate = {
        addresses: []
      }
      userAddressUpdate.addresses.push(this.user.addressInvoice);
      // self.$store.dispatch('setUser',userAddressUpdate);
    }
  },
  created: function(){
  }
}
</script>

